.app{
  height: 100vh;
}
.maincontainer{
  display:flex;
  height: calc(100vh - 5rem);
  padding-top: 2rem;
  overflow: visible;
}
.side{
  float: left;
  overflow: visible;
}
.main{
  margin-top: 3rem;
  height: calc(100vh - 10rem);
  flex:4 1;
  overflow: visible;
  margin-right: 2rem;
}

@media only screen and (max-width:1000px) {
  .side {
    display: none;
  }
  .main {
    margin-right: 0;
  }
}


.menucontainer{
    display: flex;
    width: 18rem;
    height: 100%;
    background-color: white;
    overflow: hidden;
    justify-content: left;
}

.menubutton:hover{
    background-color: #FBE9E7;
    cursor: pointer;
}
.options{
    margin-left:2.4rem;
    margin-top: 5.5rem;
}
.pages{
    list-style: none;
    margin: 1rem 0;
    padding: 0.5rem 1rem;
    width: 10rem;
    cursor: pointer;
    border-radius: 1rem;
    align-items: center;
    justify-content: left;
    display: flex;
    color: #757575;
}

.pages p{
    padding-left: 0.75rem;
}
.pages:hover{
    background-color: #FBE9E7;
    color:#D84315;
}

.navlink.selected .pages{
    background-color: #FBE9E7;
    color:#D84315;
}


.logos{
    width: 100%;
    display: flex;
    justify-content: right;
    align-items: center;
}
.header{

    width: 100%;
    align-items: center;
    height: 5rem;
    background-color: white;
    position: fixed;
    top:0;
    z-index: 999;
}
.container{
    height: 100%;
    margin: 0 2.5rem;
    display:flex;
    align-items: center;
    justify-content:left;
    flex-direction: row;
}

.menubutton{
    margin-left:2rem;
    border-radius: 3px;
    color:#D84315;
    display: flex;

}



.menu:hover{
    background-color: #FBE9E7;
}


.logo{
    color:#D84315;
    font-weight: bold;
    font-size:2rem;
   
    -webkit-user-select: none;
   
        -ms-user-select: none;
   
            user-select: none;
}

@media only screen and (min-width: 1000px){
    .mobilemenu{
        display:none;
  
    }
}
.collection{
    width: 100%;
    height: calc(100vh - 8rem);
    z-index: 99;
    justify-content: center;
    
}
.collectioncontainer{
    height: 100%;
    width: 100%;
    display: flexbox;
    justify-content: center;
    align-items: center;
}

.displayholder{
    padding-top: 2rem;
    height: 100%;
    display: flex;
    overflow-y: scroll;
    background: #FBE9E7;
   border-radius: 1rem 0 0 0 ;
   width: 100%;
   justify-content: center;
}   

.sort{
    position:absolute;
    right:3rem;
    bottom:3rem;
}
.recommended{
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
    -webkit-filter: brightness(100%);

}
.recommended:hover{
    -webkit-transform:scale(1.2);
            transform:scale(1.2);
    cursor:pointer;
    -webkit-filter: brightness(40%);
    transition: all 1s ease;
}

.noitem{
 
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

html{
    
}
.searchfield {
    background-color: #FBE9E7;
    width: 100%;
    height: inherit;
    z-index: 99;
    padding: 2rem 0;
    border-radius: 1rem 0 0 0;
    overflow-y: scroll;
    scroll-behavior: smooth;
}


.resultholder {
    display: flex;
    height: 100%;
    overflow-x: hidden;
    justify-content: center;
    background-color: #E0E0E0;
    margin: 10px 2vw;
    border-radius: 1rem;

}
.resultcontent{
    display: flex;
    height: 100%;
    justify-content: center;

    width: 100%;
    border-radius: 1rem;
    overflow-y: auto;

}

.infoholder {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.paperholder {
    justify-content: center;
    flex-direction: row;
    height: 100%;
    flex-wrap: wrap;
    overflow-y: scroll;

}

.suggestionholder{
    width: 100%;
    display:flex;
    justify-content: center;
}
.noresults{
    padding-top: 60%;
    
}

.thelist{
    display: flex;
    flex-direction: column;
    width: 100%;
  
    align-items: center;
    overflow-y: auto;
 
}
.currentdisplay{
    flex:1 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.animedetails{
    display: flex;
    flex-direction: row;
    height:100%;
}

.detailbox{
    flex: 1 1;
 
    margin: 2rem 5vw 2rem 0;
}


.characters{
    margin: 0 5vw;
    width: 100%;
    overflow-x: auto;
}

.dragcircle{
    background-color: #D84315;
    color:white;
    text-align: center;
    position:absolute;
    z-index: 999;
    display: none;
    height: 3.5rem;
    width: 3.5rem;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;

    border-radius: 50%;
}

.dragcircle.on{
    display: flex;
    justify-content: center;
    align-items: center;

}

.charactercontent{
    width: 100%;
    height: 100%;
    overflow-x: auto;
    border-radius: 1rem;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.cardarea{
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
}

.cardarea:hover{
    -webkit-transform:scale(0.95);
            transform:scale(0.95);
}

@media only screen and (max-width:1200px){
    .animedetails{
        flex-direction: column;

    }
    .detailbox{
        margin: 1rem 5vw;
    }
 
}

@media only screen and (min-width:1200px){
    .animedetails{
        flex-direction: column;

    }
    .detailbox{
        margin: 1rem 2rem;
    }
}
@media only screen and (max-width:1000px) {
    .side{
        flex: 0 1;
        
    }
}
.tableholder{
    display:flex;
    justify-content: center;
    align-items: center;
}
